import React, { useEffect, useRef, useState } from 'react'
import styled from 'styled-components'
import { graphql } from 'gatsby'
import algoliasearch from 'algoliasearch/lite'
import {
  connectSearchBox,
  connectStateResults,
  InstantSearch,
} from 'react-instantsearch-dom'

import { desktopVw, mobileVw } from '../styles/utils'
import { checkIfLiveProduct, getCookie } from '../utils'

import SEO from '../components/SEO'
import ProductGrid from '../components/ProductGrid'
import ProductThumbnail from '../components/ProductThumbnail'
import RichText from '../components/RichText'
import eventTracker from '../trackers/EventTracker'
import EventType from '../trackers/eventType'
import SearchBox from '../components/SearchBox'
import { PageLoader } from '../components/PageLoader'
import { fetchProductFeed, fetchProductsInBatch } from '../utils/api'
import useDetectDevices from '../hooks/useDetectDevices'
import { ANALYTIC_COOKIES } from '../utils/constants'
import { extractProductJsonFromAPIProduct } from '../utils/build'

const DebouncedSearchBox = connectSearchBox(SearchBox)

const StyledSearch = styled.section`
  margin-bottom: 40px;
  background: ${props => props.theme.colors.secondary};
  z-index: 1100;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: flex;
    flex-direction: column;
    height: 100%;
    margin-bottom: ${desktopVw(50)};
  }
`

const SearchBoxWrapper = styled.div`
  display: flex;
  justify-content: center;
  padding: 0 10px;

  * {
    -webkit-appearance: none;
  }

  .ais-SearchBox-submit,
  .ais-SearchBox-reset,
  button {
    display: none;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: 0 ${desktopVw(80)};

    form {
      display: flex;
    }
  }
`

const ProductsCount = styled.span`
  display: none;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: flex;
    margin-bottom: ${desktopVw(37)};
    padding: 0 ${desktopVw(80)};
    font-size: ${desktopVw(10)};
    color: ${props => props.theme.colors.greyLight};
    text-transform: uppercase;
  }
`

const HitsWrapper = styled.div``

const NoResults = styled(RichText)`
  text-align: center;
  text-transform: uppercase;

  p {
    font-weight: 500;
    font-family: ${props => props.theme.fonts.heading};
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(40)};
    p {
      font-size: ${desktopVw(20)};
    }
  }
`

const RecommendedAside = styled.aside`
  margin-bottom: ${desktopVw(90)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-top: ${desktopVw(-150)};
  }
`

const RecommendedTitle = styled.h2`
  font-size: 48px;
  font-family: ${props => props.theme.fonts.secondary};
  text-transform: uppercase;
  text-align: center;
  line-height: 70px;
  margin-bottom: 20px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(64)};
    line-height: ${desktopVw(210)};
    margin-bottom: 0;
    letter-spacing: -${desktopVw(4)};
  }
`

const RecommendedList = styled.ul`
  display: flex;
  overflow-x: scroll;

  &::-webkit-scrollbar {
    display: none;
  }
  padding: 0 ${desktopVw(40)};
`

const RecommendedItem = styled.li`
  padding-right: 10px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    min-width: ${desktopVw(360)};
    padding-right: ${desktopVw(40)};
  }
`

const StyledLoader = styled(PageLoader)`
  padding: ${mobileVw(240)} 0;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(150)} 0;
  }
`

const searchClient = algoliasearch(
  process.env.GATSBY_ALGOLIA_APP_ID,
  process.env.GATSBY_ALGOLIA_SEARCH_KEY,
)

const countProductsByColor = products => {
  let totalCount = 0
  products
    .filter(product => checkIfLiveProduct(product))
    .forEach(product => {
      if (product.colors) {
        product.colors.forEach(color => {
          const colorPresent = product.productImages.filter(
            pi => pi.color.name === color.name,
          )
          if (colorPresent.length > 0) totalCount++
        })
      }
    })
  return totalCount
}

const Hits = connectStateResults(
  ({ searchResults, searchState, setHasInput, allProducts, placeholder }) => {
    const hasInput = searchState.query && searchState.query !== ''
    const nbHits = searchResults && searchResults.nbHits
    const hasResults = nbHits !== 0
    const searchTerm = useRef(null)
    const { devices } = useDetectDevices()

    const [loading, setLoading] = useState(false)
    const [contentfulProducts, setContentfulProducts] = useState([])

    useEffect(() => {
      setHasInput(hasInput)
    }, [hasInput])

    useEffect(() => {
      if (searchResults && searchResults.hits.length > 0) {
        const productIds = searchResults.hits.map(hit => hit.product_id)
        ;(async () => {
          const currentSearchTerm = searchTerm.current
          if (currentSearchTerm) {
            setLoading(true)
            const products = await fetchProductsInBatch(productIds)
            if (currentSearchTerm === searchTerm.current) {
              setContentfulProducts(products)
              setLoading(false)
            }
          } else {
            setContentfulProducts([])
            setLoading(false)
          }
        })()
      }
    }, [searchResults, searchResults?.hits])

    useEffect(() => {
      searchTerm.current = searchState.query
    }, [searchState.query])

    const products = contentfulProducts
      .filter(product => product.shopifyProduct)
      .filter(product => checkIfLiveProduct(product))

    const totalItems = countProductsByColor(products)

    useEffect(() => {
      if (!searchState.query || !searchState.query.trim()) {
        return
      }
      eventTracker.track(EventType.SEARCH, {
        searchQuery: searchState.query.trim(),
      })
    }, [searchState.query])

    if (loading) return <StyledLoader />

    return (
      <>
        {hasInput && (
          <>
            {hasResults && totalItems > 0 ? (
              <HitsWrapper>
                <ProductsCount>
                  {totalItems >= 1 ? `${totalItems} items` : `1 item`}
                </ProductsCount>
                <ProductGrid
                  products={products}
                  tracker='search_page'
                  applyDebounce
                />
              </HitsWrapper>
            ) : (
              <NoResults json={placeholder.json} />
            )}
          </>
        )}
      </>
    )
  },
)

const Search = ({ data }) => {
  const page = data.contentfulPage
  const { nodes } = data.allContentfulProduct ? data.allContentfulProduct : {}

  const [hasInput, setHasInput] = useState(false)

  const [recommendedProducts, setRecommendedProducts] = useState(null)

  const { devices } = useDetectDevices()

  useEffect(() => {
    ;(async () => {
      const email =
        getCookie(ANALYTIC_COOKIES.LB_CUSTOMER_EMAIL) ||
        getCookie(ANALYTIC_COOKIES.LB_CUSTOMER_TENTATIVE_EMAIL) ||
        ''
      const userId = getCookie(ANALYTIC_COOKIES.LB_USER_ID) || ''

      const url = `${
        process.env.GATSBY_QUINCE_STORE_FRONT_API
      }/get-product-feed?limit=10${email ? `&email=${email}` : ''}${
        userId ? `&lbUserId=${userId}` : ''
      }`
      const response = await fetchProductFeed(url)
      setRecommendedProducts(response?.products)
    })()
  }, [])

  return (
    <>
      <SEO
        title={page.seoTitle}
        description={page.seoDescription}
        type={page.pageType}
        slug={page.slug}
        image={page.metaImage ? page.metaImage.fluid.src : null}
      />
      <StyledSearch>
        <InstantSearch
          indexName={process.env.GATSBY_ALGOLIA_INDEX_NAME}
          searchClient={searchClient}
        >
          <SearchBoxWrapper hasInput={hasInput}>
            <DebouncedSearchBox
              placeholder={
                devices.desktop
                  ? 'Search for categories, products, styles, material or anything else'
                  : 'Search for anything'
              }
              delay={500}
            />
          </SearchBoxWrapper>
          <Hits
            setHasInput={setHasInput}
            allProducts={nodes}
            placeholder={page.placeholder}
          />{' '}
        </InstantSearch>
      </StyledSearch>
      {hasInput && recommendedProducts && (
        <RecommendedAside>
          <RecommendedTitle>You may like</RecommendedTitle>
          <RecommendedList>
            {recommendedProducts
              .filter(
                product =>
                  product.live === true ||
                  process.env.GATSBY_QUINCE_INCLUDE_NON_LIVE_PRODUCTS ===
                    'true',
              )
              .map((product, i) => (
                <RecommendedItem key={product.id}>
                  <ProductThumbnail
                    title={product.title || product.shopifyProduct.title}
                    contentfulProduct={product}
                    staticPrice={
                      product.shopifyProduct.variants.edges[0].node.price
                    }
                    tracker={`search_page__recommended__${i}`}
                  />
                </RecommendedItem>
              ))}
          </RecommendedList>
        </RecommendedAside>
      )}
    </>
  )
}

export default Search

export const SearchQuery = graphql`
  query Search($id: String!) {
    contentfulPage(id: { eq: $id }) {
      id
      seoTitle
      seoDescription
      metaImage {
        fluid(maxWidth: 1200) {
          ...GatsbyContentfulFluid_withWebp
        }
      }
      slug
      placeholder {
        json
      }
      recommendedProducts {
        title
        slug
        subtitle
        live
        gender
        metaTags
        shippingInformation {
          shippingInformation
        }
        shippingMetaInfoText {
          shippingMetaInfoText
        }
        productImages {
          color {
            name
          }
          images {
            id
            description
            fluid(maxWidth: 1440) {
              ...GatsbyContentfulFluid_withWebp
            }
          }
        }
        colors {
          name
          hexcode
        }
        shopifyProduct {
          ...ShopifyProduct
        }
      }
    }
  }
`
