import React, { useEffect, useState, useRef } from 'react'
import styled from 'styled-components'

import { desktopVw, mobileVw, tabletVw } from '../styles/utils'

import ProductThumbnail from './ProductThumbnail'
import useFetchInventoryTag, {
  InventoryTag,
} from '../hooks/useFetchInventoryTag'
import useDebounce from '../hooks/useDebounce'

const StyledProductGrid = styled.div`
  position: relative;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(92)};
    padding: 0 ${desktopVw(80)};
  }
`

const ProductItem = styled.li`
  width: calc(50% - 10px);
  margin: 0 0 25px;
  padding: 0 5px;
  opacity: 1;
  transform: translateY(0);

  .product-thumbnail__image-wrapper {
    height: ${mobileVw(212)};
    width: 100%;
  }

  @media (min-width: ${props => props.theme.breakpoints.tablet}px) {
    .product-thumbnail__image-wrapper {
      height: ${tabletVw(450)};
    }
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: calc(25% - (1 / 4 * ${desktopVw(121)}));
    margin: 0 ${desktopVw(40)} ${desktopVw(43)} 0;
    padding: 0;

    .product-thumbnail {
      &__image-wrapper {
        height: ${desktopVw(357.4)};
      }
    }
  }
`

const Products = styled.ul`
  display: flex;
  flex-flow: wrap;
  padding: 0 5px;

  ${ProductItem} {
    &:nth-child(5n) {
      width: calc(100% + 10px);
      margin: 0 -5px 18px;
      padding: 0;

      .product-thumbnail {
        &__image-wrapper {
          height: 437px;
        }

        &__footer {
          padding: 0 10px;
        }
      }
    }
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: 0;

    ${ProductItem} {
      &:nth-child(5n) {
        width: calc(25% - (1 / 4 * ${desktopVw(121)}));
        margin: 0 ${desktopVw(40)} ${desktopVw(43)} 0;

        .product-thumbnail {
          &__image-wrapper {
            height: ${desktopVw(357.4)};
          }

          &__footer {
            padding: 0;
          }
        }
      }

      &:nth-child(4n) {
        margin-right: 0;
      }
    }
  }
`

const ProductGrid = ({
  className,
  products,
  tracker,
  animationDelay,
  applyDebounce = false,
  metaProducts,
  prefetchColor = true,
}) => {
  const [colorsToProducts, setColorsToProducts] = useState(
    splitProductsByColor(products, metaProducts, prefetchColor),
  )

  const { enrichProductsByColorWithInventoryTag } = useFetchInventoryTag()

  useEffect(() => {
    if (!products || products.length == 0 || !products[0]) return

    setColorsToProducts(
      splitProductsByColor(products, metaProducts, prefetchColor),
    )
  }, [products])

  useEffect(() => {
    if (!metaProducts || metaProducts.length == 0 || !metaProducts[0]) return

    setColorsToProducts(
      splitProductsByColor(products, metaProducts, prefetchColor),
    )
  }, [metaProducts])

  // This helps knowing if its the first mount call, this helps in ensuring that in case debounce is applied,
  // we are not making call on did mount action.
  const didMount = useRef(false)

  // Pulling inventory tags with debounce to avoid multiple calls,
  // this is needed because of Search that triggers component re-render
  // multiple time (Roughly 20 times)
  useEffect(() => {
    // In case of using debounce, the api calls happens for component did mount and last render,
    // in order to avoid first call doing this.
    if (!didMount.current && applyDebounce) {
      didMount.current = true
      return
    }
    // TODO: Remove this dirty check
    if (!products || products.length == 0 || !products[0]) {
      return
    }
    const updatedColorsToProducts = splitProductsByColor(
      products,
      metaProducts,
      prefetchColor,
    )
    enrichProductsByColorWithInventoryTag(updatedColorsToProducts).then(
      productRows => {
        setColorsToProducts(productRows)
      },
    )
  }, [
    useDebounce(products, applyDebounce ? 1000 : 0),
    useDebounce(metaProducts, applyDebounce ? 1000 : 0),
  ])

  return (
    <StyledProductGrid className={`${className} product-grid`}>
      <Products className='product-grid__list'>
        {colorsToProducts.map((colorsToProduct, i) => {
          const count = i % 18
          return (
            <ProductItem
              key={`${colorsToProduct.p.id}${i}`}
              className='product-grid__item'
              count={count}
              initialDelay={i <= 17 ? 1000 : 500}
              animationDelay={animationDelay}
            >
              <ProductThumbnail
                title={
                  colorsToProduct.p.title ||
                  colorsToProduct.p.shopifyProduct.title
                }
                contentfulProduct={colorsToProduct.p}
                staticPrice={
                  colorsToProduct.p.shopifyProduct.variants.edges[0].node.price
                }
                tracker={tracker ? `${tracker}__${i}` : undefined}
                prefetchedColor={prefetchColor && colorsToProduct.c}
              />
            </ProductItem>
          )
        })}
      </Products>
    </StyledProductGrid>
  )
}

const splitProductsByColor = (products, metaProducts, prefetchColor) => {
  const splitProductsByColor = []

  const handleColorAdd = (product, productColor, colors, productImages) => {
    const colorPresent = product.productImages.filter(
      pi => pi.color.name === productColor,
    )
    if (colorPresent.length > 0) {
      splitProductsByColor.push({
        c: colors.find(color => color.name === productColor),
        p: { ...product }, // Shallow copy to avoid reference updates
        pi: productImages.filter(pi => pi.color.name === productColor),
      })
    } /* Muting this for now as it is possible colors are not added
      else {
      captureMessage(
        `Color ${productColor} does not exist in the product images - ${product.title ||
          product.shopifyProduct.title}`,
      )
    } */
  }

  if (metaProducts && metaProducts.length > 0) {
    metaProducts.forEach(({ colors, product }) => {
      // Removing inventory related tags and fetching them from API.
      product.tags = (product.tags || []).filter(
        t =>
          ![
            InventoryTag.BACK_IN_STOCK,
            InventoryTag.LOW_STOCK,
            InventoryTag.SOLD_OUT,
          ].includes(t),
      )
      if (!prefetchColor)
        handleColorAdd(product, colors[0].name, colors, product.productImages)
      else
        colors.forEach(color => {
          handleColorAdd(product, color.name, colors, product.productImages)
        })
    })
  } else if (products && products.length > 0) {
    products.forEach(product => {
      if (product) {
        const { color: productColor, colors, productImages } = product
        if (colors && colors.length > 0) {
          // Removing inventory related tags and fetching them from API.
          product.tags = (product.tags || []).filter(
            t =>
              ![
                InventoryTag.BACK_IN_STOCK,
                InventoryTag.LOW_STOCK,
                InventoryTag.SOLD_OUT,
              ].includes(t),
          )
          if (!productColor) {
            if (!prefetchColor) {
              const colorPresent = product.productImages.filter(
                pi => pi.color.name === product.colors[0].name,
              )
              if (colorPresent.length > 0) {
                splitProductsByColor.push({
                  c: product.colors[0],
                  p: { ...product }, // Shallow copy to avoid reference updates
                  pi: product.productImages.filter(
                    pi => pi.color.name === product.colors[0].name,
                  ),
                })
              }
            } else
              product.colors.forEach((color, i) => {
                const colorPresent = product.productImages.filter(
                  pi => pi.color.name === color.name,
                )
                if (colorPresent.length > 0) {
                  splitProductsByColor.push({
                    c: color,
                    p: { ...product }, // Shallow copy to avoid reference updates
                    pi: product.productImages.filter(
                      pi => pi.color.name === color.name,
                    ),
                  })
                } /* Muting the sentry capture for now as it is possible colors don't exist
              else {
                captureMessage(
                  `Color ${
                    color.name
                  } does not exist in the product images - ${product.title ||
                    product.shopifyProduct.title}`,
                )
              } */
              })
          } else {
            handleColorAdd(product, productColor, colors, productImages)
          }
        } /* Muting Sentry capture
        else {
          captureMessage(
            `No Color exists on the product - ${product.title ||
              product.shopifyProduct.title}`,
          )
        } */
      }
    })
  }
  return splitProductsByColor
}

export default ProductGrid
