import React, { Component } from 'react'
import { debounce } from '../utils'
import styled from 'styled-components'
import { desktopVw } from '../styles/utils'

const Title = styled.h1`
  font-family: ${({ theme }) => theme.fonts.secondary};
  text-transform: uppercase;
  font-weight: 500;
  margin-bottom: 15px;
  font-size: 48px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(64)};
  }
`

const Container = styled.div`
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%;
  padding: 27px 0;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(45)} ${desktopVw(232)};
  }
`

const StyledInput = styled.input`
  width: 80%;
  font-family: ${props => props.theme.fonts.primary};
  background: #f4f4f4;
  padding: 15px 25px;
  border: none;
  font-size: 15px;

  &[type='search']::-webkit-search-decoration,
  &[type='search']::-webkit-search-cancel-button,
  &[type='search']::-webkit-search-results-button,
  &[type='search']::-webkit-search-results-decoration {
    -webkit-appearance: none;
  }

  &::placeholder {
    color: ${props => props.theme.colors.greyLight};
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: 100%;
    margin-top: ${props => props.hasInput && desktopVw(30)};
    font-size: ${desktopVw(15)};
  }
`

class SearchBox extends Component {
  constructor(props) {
    super(props)
    this.callRefine = debounce(this.callRefine, props.delay)
    this.state = {
      value: this.props.currentRefinement,
    }
  }

  callRefine = value => {
    this.props.refine(value)
  }

  onChangeDebounced = event => {
    const value = event.currentTarget.value

    this.callRefine(value)

    this.setState(() => ({
      value,
    }))
  }

  render() {
    const { value } = this.state
    const { placeholder } = this.props

    return (
      <Container>
        <Title>Search</Title>
        <StyledInput
          value={value}
          onChange={this.onChangeDebounced}
          placeholder={placeholder}
        />
      </Container>
    )
  }
}

export default SearchBox
